/* ibm-plex-mono-300 - latin */
@font-face {
	font-family: 'IBM Plex Mono';
	font-style: normal;
	font-weight: 300;
	src: url('ibm-plex-mono/ibm-plex-mono-v5-latin-300.eot');
	/* IE9 Compat Modes */
	src: local('IBM Plex Mono Light'), local('IBMPlexMono-Light'),
	url('ibm-plex-mono/ibm-plex-mono-v5-latin-300.eot?#iefix') format('embedded-opentype'),
	/* IE6-IE8 */
	url('ibm-plex-mono/ibm-plex-mono-v5-latin-300.woff2') format('woff2'),
	/* Super Modern Browsers */
	url('ibm-plex-mono/ibm-plex-mono-v5-latin-300.woff') format('woff'),
	/* Modern Browsers */
	url('ibm-plex-mono/ibm-plex-mono-v5-latin-300.ttf') format('truetype'),
	/* Safari, Android, iOS */
	url('ibm-plex-mono/ibm-plex-mono-v5-latin-300.svg#IBMPlexMono') format('svg');
	/* Legacy iOS */
}

